import Page from 'components/Page';
import React from 'react';
import CommonTestData from '../TestData/CommonTestData.json';
import ConfigurationFile from '../TestData/Configuration.json';
import APICon from '../TestData/ServerAddress.json';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
  FormFeedback,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from 'reactstrap';
import Loader from 'react-loader-spinner'
var APIBasePath =APICon.APIURL;
var ReportPublishPath =APICon.ReportURL;
function Options({ options }) {
    return (
        options.map(option => 
                    <option key={option.Environment}>                                   
                    {option.Environment}
                    </option>)
                   );
}

function addSpinner()
{
  return(
    <Loader
       type="Puff"
       color="#00BFFF"
       height={50}
       width={10000}
       timeout={1200000}

    />
   );
}

class Configuration extends React.Component{

  constructor(props){
    super(props);
    this.state=
    {
       DefaultEnvironment : ConfigurationFile.DefaultEnvironment,
       defaultBrowser : ConfigurationFile.DefaultBrowser,
       checkDashboardHistoryCount:false,
       feedbackDashboardHistoryCount:'',
       DashBoardHistoryCount:ConfigurationFile.DashboardHistoryCount,
       checkDashboardDays:false,
       feedbackDashboardDays:'',
       DashboardDays:ConfigurationFile.DashboardTotalDayCount,
       HubMachineName:ConfigurationFile.HUBMachineName,
       checkHubMachineName:false,
       feedbackHubMachineName:'',
       HubPortNumber:ConfigurationFile.HUBPort,
       checkHubPort:false,
       feedbackHubPort:''
    }
    
  }
  UpdateEnv(event)
  {
    this.setState({isLoaded:false})
    this.setState({DefaultEnvironment: event.target.value})
    this.setState({isLoaded:true})
  }
  UpdateBrowser(event)
  {
    this.setState({isLoaded:false})
    this.setState({defaultBrowser: event.target.value})
    this.setState({isLoaded:true})
  }

  UpdateDashBoardHistoryCount(event)
  {
    this.setState({isLoaded:false})
    this.setState({checkDashboardHistoryCount: false})
    this.setState({DashBoardHistoryCount: event.target.value})
    this.setState({isLoaded:true})
  }

  UpdateHubMachineName(event)
  {
    this.setState({isLoaded:false})
    this.setState({checkHubMachineName: false})
    this.setState({HubMachineName: event.target.value})
    this.setState({isLoaded:true})
  }
  UpdateHubPort(event)
  {
    this.setState({isLoaded:false})
    this.setState({checkHubPort: false})
    this.setState({HubPortNumber: event.target.value})
    this.setState({isLoaded:true})
  }
  UpdateDashBoardDays(event)
  {
    this.setState({isLoaded:false})
    this.setState({checkDashboardDays: false})
    this.setState({DashboardDays: event.target.value})
    this.setState({isLoaded:true})
  }
  SaveConfig()
  {
     var env = this.state.DefaultEnvironment;
     var Browser = this.state.defaultBrowser;
     var HistoryCount = this.state.DashBoardHistoryCount;
     var DashboardDaycount = this.state.DashBoardHistoryCount;
     var ExceptionMessage ='';
     console.log(HistoryCount);
     if(isNaN(HistoryCount))
     {
      this.setState({checkDashboardHistoryCount:true})
      this.setState({feedbackDashboardHistoryCount:'Dashboard History Count* takes only numeric value.'})
      ExceptionMessage= ExceptionMessage+ 'Dashboard History Count* takes only numeric value..'
     }
     console.log(DashboardDaycount);
     if(isNaN(DashboardDaycount))
     {
      this.setState({checkDashboardDays:true})
      this.setState({feedbackDashboardDays:'Dashboard Total Day Count* takes only numeric value.'})
      ExceptionMessage= ExceptionMessage+ 'Dashboard Total Day Count* takes only numeric value..'
     }
     if(ExceptionMessage.trim() !=="")
     {
      // this.setState({modal:true})
       return ;
     }
     var CommonJObject ={};
     CommonJObject["DefaultEnvironment"]=env;
     CommonJObject["DefaultBrowser"]=Browser;
     CommonJObject["DashboardHistoryCount"]=HistoryCount;
     CommonJObject["DashboardTotalDayCount"]=DashboardDaycount;

     var configjson = JSON.stringify(CommonJObject)
     var configAPI =APIBasePath+'configuration';
     var requestOptions = {
       method: 'POST',
      headers: {"Accept": "application/json; odata=verbose",'Content-type': 'application/x-www-form-urlencoded'},
       body: configjson
     };
     const configurationRequest = async () => 
     {
       const ORResponse = await fetch(configAPI,requestOptions);
       const ORJson = await ORResponse.json();
       if(ORJson.success.toLowerCase()==='true')
       {
       this.setState({modal:true})
       this.setState({modalValidationText:ORJson.servermessage})
       }
       else
       {
       this.setState({modal:true})
       this.setState({modalValidationText:ORJson.servermessage})
       }
       
       
     }
     configurationRequest();

  }

  SaveHUBconfig()
  {
    var ExceptionMessage='';
    var hub = this.state.HubMachineName;
    var port = this.state.HubPortNumber;
    if(hub.trim()==="")
    {
     this.setState({checkHubMachineName:true})
     this.setState({feedbackHubMachineName:'Hub Machine Name* can not be blank'})
     ExceptionMessage= ExceptionMessage+ 'Hub Machine Name* can not be blank'
    }
    if(isNaN(port))
    {
     this.setState({checkHubPort:true})
     this.setState({feedbackHubPort:'Port Number* takes only numeric value.'})
     ExceptionMessage= ExceptionMessage+ 'Port Number* takes only numeric value..'
    }
    if(port.trim()==="")
    {
     this.setState({checkHubPort:true})
     this.setState({feedbackHubPort:'Port Number* can not be blank'})
     ExceptionMessage= ExceptionMessage+ 'Port Number* can not be blank'
    }
    if(ExceptionMessage.trim() !=="")
    {
      return ;
    }
    var CommonJObject ={};
    CommonJObject["HUBMachineName"]=hub;
    CommonJObject["HUBPort"]=port;
    var configjson = JSON.stringify(CommonJObject)
    var configAPI =APIBasePath+'configuration?id=1';
    var requestOptions = {
      method: 'POST',
     headers: {"Accept": "application/json; odata=verbose",'Content-type': 'application/x-www-form-urlencoded'},
      body: configjson
    };
    const configurationRequest = async () => 
    {
      const ORResponse = await fetch(configAPI,requestOptions);
      const ORJson = await ORResponse.json();
      if(ORJson.success.toLowerCase()==='true')
      {
      this.setState({modal:true})
      this.setState({modalValidationText:ORJson.servermessage})
      }
      else
      {
      this.setState({modal:true})
      this.setState({modalValidationText:ORJson.servermessage})
      }
      
      
    }
    configurationRequest();
  }

  toggle = modalType => () => {
    if (!modalType) {
      return this.setState({
        modal: !this.state.modal,
      });
    }

    this.setState({
      [`modal_${modalType}`]: !this.state[`modal_${modalType}`],
    });
  };

  render() {

    var {isLoaded} = this.state;
  
    if(!isLoaded)
    {
      addSpinner();
    }
  return (
    <Page title="Configuration Setup" breadcrumbs={[{ name: 'Configuration', active: true }]}>
	 <Row>
        <Col lg={6} md={6} sm={6} xs={12}>
          <Card>
            <CardHeader>Execution Setup</CardHeader>
            <CardBody>
              <Form>
                <FormGroup row>
                  <Label for="exampleSelect" sm={6}>
                    Default Environment*
                  </Label>
                  <Col>
                    <Input type="select" name="envlist" value ={this.state.DefaultEnvironment} onChange={this.UpdateEnv.bind(this)}>
					             <Options options={CommonTestData} />
					          </Input>
                  </Col>
                </FormGroup>
				        <FormGroup row>
                  <Label for="exampleSelect" sm={6}>
                    Default Browser*
                  </Label>
                  <Col >
                    <Input type="select" name="defaultbrowser" value ={this.state.defaultBrowser} onChange={this.UpdateBrowser.bind(this)}>
					          <option>CHROME</option>
                    <option>FIREFOX</option>
					          </Input>
                  </Col>
                </FormGroup>
                <FormGroup row>
                  <Label for="exampleSelect" sm={6}>
                    Dashboard History Count*
                  </Label>
                  <Col >
                    <Input invalid={this.state.checkDashboardHistoryCount} type="input" name="DashboardHistory"  value ={this.state.DashBoardHistoryCount} onChange={this.UpdateDashBoardHistoryCount.bind(this)}/>
                    <FormFeedback>
                      {this.state.feedbackDashboardHistoryCount}
                    </FormFeedback>
                  </Col>
                </FormGroup>
                <FormGroup row>
                  <Label for="exampleSelect" sm={6}>
                    Dashboard Total Day Count*
                  </Label>
                  <Col >
                    <Input invalid={this.state.checkDashboardDays} type="input" name="DashboardDay"  value ={this.state.DashboardDays} onChange={this.UpdateDashBoardDays.bind(this)}/>
                    <FormFeedback>
                      {this.state.feedbackDashboardDays}
                   </FormFeedback>
                  </Col>
                </FormGroup>
                <Col >
                  <Button  onClick={this.SaveConfig.bind(this)} color="primary" name ="SaveConfiguration">Save</Button>
                  </Col>
                  <Modal
                isOpen={this.state.modal}
                toggle={this.toggle()}
                className={this.props.className}>
                <ModalHeader  toggle={this.toggle()}> Information</ModalHeader>
                <ModalBody >
                  {this.state.modalValidationText}
                </ModalBody>
                <ModalFooter>
                  <Button color="secondary" onClick={this.toggle()}>
                    OK
                  </Button>
                </ModalFooter>
              </Modal>
              </Form>
            </CardBody>
          </Card>
        </Col>

        <Col lg={6} md={6} sm={6} xs={12}>
          <Card>
            <CardHeader>Selenium Grid Setup</CardHeader>
            <CardBody>
              <Form>
                <FormGroup row>
                  <Label for="exampleSelect" sm={6}>
                    Hub Machine IP*
                  </Label>
                  <Col>
                  <Input invalid={this.state.checkHubMachineName} type="input" name="DashboardHistory"  value ={this.state.HubMachineName} onChange={this.UpdateHubMachineName.bind(this)}/>
                    <FormFeedback>
                      {this.state.feedbackHubMachineName}
                    </FormFeedback>
                  </Col>
                </FormGroup>
				        <FormGroup row>
                  <Label for="exampleSelect" sm={6}>
                    Port Number*
                  </Label>
                  <Col >
                     <Input invalid={this.state.checkHubPort} type="input" name="DashboardHistory"  value ={this.state.HubPortNumber} onChange={this.UpdateHubPort.bind(this)}/>
                      <FormFeedback>
                      {this.state.feedbackHubPort}
                    </FormFeedback>
                  </Col>
                </FormGroup>
                <Col >
                  <Button  onClick={this.SaveHUBconfig.bind(this)} color="primary" name ="SaveConfiguration">Save</Button>
                 </Col>
              </Form>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </Page>
  );
  }
};

export default Configuration;
